    .Header
    {
        background-color: #1A2238 ;
        color: #FF6A3D;
        text-align: center;
        font-size:x-large;
        display: flex;
        /* padding-bottom: 1rem; */
        margin-bottom: 1rem;
    }
    
    .Header h1
    {
        text-align: left;
        padding-left: 10px;
        width: 50%;
        font-size: xx-large;
    }
    
    .Header p
    {
        text-align: right;
        width: 50%;
        font-size: large;
        padding-right: 30px;
        padding-top: 20px;  
    }

.buttons 
{
    cursor: pointer;
    font-family: 'Montserrat', sans-serif; 
    text-align: center;
    background: 0;
    border: 0;
    padding-left: 6%;
    padding-right: 5%;
    color: #9DAAF2;
    font-size: 1.5rem;
    font-weight: 900;
    margin-right: 0.25em;
    transition: opacity 250ms ease-in;
}
.buttons:hover 
{opacity: 0.7} 


.button
{
    display: flex;
    background-color: #1A2238;
    border-radius: 25px;
    margin-top: 2%;
    margin-left: 22%;
    margin-right: 30%;
    margin-bottom: 2%;
    padding-bottom: 0.5%;
    padding-top: 0.25%;
    padding-left: 1%;
}

.MAIN
{
    padding-left: 30%;
}

.submitButton
{
    margin-left: 44%;
    padding: 0;
    color: #f4db7b;
}

.wrong
{
    /* text-align: center; */
    color:#f4db7b;
    font-size: x-large;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    margin-left: 42%;
}

.correct
{
    color: #f4db7b;
    font-size: x-large;
    margin-top: 00.5rem;
